#home-box {
    width: 500px;
    height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.459);
}

.inner-box {
    width: 250px;
    height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: rgba(119, 119, 119, 0.493);
    border-radius: 10px;
    box-shadow: 0 0 10px 5px rgba(255, 255, 255, 0.5);
}

.temp {
    width: auto;
    height: auto;
    font-size: xxx-large;
    color: white;
}

#select-box {
    width: 190px;
    height: 20px;
    color: rgb(0, 0, 0);
    background-color: rgba(255, 255, 255, 0.541);
    border-radius: 5px;
}

option {
    border-radius: 10px;
    background-color: rgba(138, 138, 138, 0.548);
}

option:active {
    background-color: white;
}